
// Əlaəqni cədvəldən silmə funksiyasi
import { Modal} from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';

const { confirm } = Modal;

const showConfirm=(id,data,dispatch)=>{
  confirm({
    title: 'Bu Tələbəni silmək istəyinizdən əminsiz?',
    icon: <ExclamationCircleOutlined />,
    onOk() {
      dispatch({type:"Remove_Student",payload:id});
      data=data.filter(Person=>Person.key!==id)
    },
  });
}

export default showConfirm;