import { Routes, Route } from "react-router-dom";
import Header from "./components/Header/Header";
import { useEffect, useReducer, useState } from "react";
import reducer from "./utils/reducer";
import { MainContext } from "./context/MainContext";
import EditStudent from "./containers/EditStudent/EditStudent";
import ScaleLoader from "react-spinners/ScaleLoader";
import Students from "./containers/Students/Students";
import NewStudent from "./containers/NewStudent/NewStudent";
function App() {
  let [loading, setLoading] = useState(true);

  const [students, dispatch] = useReducer(reducer, [], () => {
    const localStudents = localStorage.getItem("Students");

    return localStudents ? JSON.parse(localStudents) : [];
  });

  useEffect(() => {
    setTimeout(()=>{
      setLoading(false)
    },2000)
    localStorage.setItem("Students", JSON.stringify(students));
  }, [students]);
  return (
    <>
     { loading?
      <div className="loading">
        <ScaleLoader color="#5dfd5d" loading={loading} size={150} />
      </div>
      :
      <div className="App">
        <Header />
        <MainContext.Provider value={{ students, dispatch }}>
          <Routes>
            <Route path="/Students" element={<Students />} />
            <Route path="/Students/new" element={<NewStudent />} />
            <Route
              path="/Students/edit/:id"
              element={<EditStudent students={students} />}
            />
          </Routes>
        </MainContext.Provider>
      </div>}
    </>
  );
}

export default App;
