import React from 'react'
import { Table } from 'antd'
import './StudentTable.scss'
import showConfirm from '../../utils/DeleteStudent'
import { Link } from 'react-router-dom'
function StudentTable({ students, dispatch }) {
  const columns = [
    {
      title: 'Ad',
      dataIndex: 'firstName',
      width: 150,
    },
    {
      title: 'Soyad',
      dataIndex: 'lastName',
      width: 150,
    },
    {
      title: 'Address',
      dataIndex: 'Address',
    },
    {
      title: 'Phone number',
      dataIndex: 'pnumber',
    },
    {
      title: 'Ixtisas',
      dataIndex: 'profession',
    },
    {
      title: 'Əməliyyatlar',
      dataIndex: 'operations',
      render: (_, record) => (
        // burada record əlaqənin özüdür və record.key əlaqənin id-dir
        <div>
        
          <Link className="clear" to={`/Students/edit/${record.key}`}>
            <i className="fa-solid fa-pen-to-square"></i>
          </Link>

          <span
            className="clear"
            onClick={() => showConfirm(record.key, data, dispatch)}
          >
            <i className="fa-solid fa-trash-can"></i>
          </span>
        </div>
      ),
    },
  ]

  let data = []
  // makeRow funksiyasi connection massivindən əlaqələri data massivinə daxil edir,
  // data massividə cedvelde göstərilir
  const makeRow = () => {
    students.map((Person) =>
      data.push({
        key: Person.id,
        firstName: Person.firstName,
        lastName: Person.lastName,
        Address: Person.Address,
        pnumber: Person.pnumber,
        profession: Person.profession,
        position: Person.position,
      }),
    )
  }
  makeRow();

  return (
    <div>
      <Table
        columns={columns}
        dataSource={data}
        pagination={data.length > 11 ? true : false}
      />
    </div>
  )
}

export default StudentTable;
