import React from 'react'
import { useParams } from 'react-router-dom'
import StudentForm from '../../components/StudentForm/StudentForm'
import './EditStudent.scss'
function EditStudent({students}) {
  const { id } = useParams()
  //Dəyişiklik ediləcək əlaqə CurrentConnection
  let CurrentStudent = students.find((Person) => Person.id === id);
  return (
    <div className="EditStudent">
      <h1>Əlaqə üzərində dəyişiklik</h1>
      <StudentForm CurrentStudent={CurrentStudent} />
    </div>
  )
}

export default EditStudent;